<template>
  <div class="autenticacao">
    <auth-template
      title="Área administrativa"
      :alert="false"
      :isBarber="false"
      goBackRoute="search"
      :userNotEnabled="false"
    >
      <card class="form login">
        <div class="form-group">
          <v-text-field
            id="login"
            v-model="name"
            class="login"
            name="login"
            dark
            label="Login"
            :rules="nameRules"
            v-on:keyup.enter="focusToNext('password')"
            :success="isNameValid(name)"
            clearable
            required
            validate-on-blur 
            @onKeyUp="setData"
          >
            <template v-slot:prepend-inner>
              <v-icon class="outlined material-icons-outlined mr-2" dense>mail</v-icon>
            </template>
          </v-text-field>
        </div>

        <div class="form-group">
          <v-text-field
            id="password"
            ref="password"
            v-model="password"
            :success="isPasswordValid(password)"
            dark
            :type="showPassword ? 'text' : 'password'"
            label="Senha"
            color="success"
            :append-icon="showPassword ? 'visibility_off' : 'remove_red_eye'"
            :rules="passwordRules"
            @click:append="showPassword = !showPassword"
            v-on:keyup.enter="enterClick"
            clearable
            name="password"
            required
            validate-on-blur 
            @onKeyUp="setData"
          >
            <template v-slot:prepend-inner>
              <v-icon class="material-icons-outlined mr-2" dense>vpn_key</v-icon>
            </template>
          </v-text-field>
        </div>

        <div class="text-center mt-8">
          <btn-primary 
            class="btnLogin"
            text="LOGAR"
            :loading="false"
            @clicked="login"
          />
        </div>  
      </card>
    </auth-template>
  </div>
</template>

<script>
import AuthTemplate from '@/components/auth/AuthTemplate'
import Card from '@/components/globais/Card'
import BtnPrimary from '@/components/auth/BtnPrimary'
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Login',
  components: { AuthTemplate, BtnPrimary, Card },
  mixins: [ FormMixin, NavigationMixin, SnackbarMixin, ValidationMixin ],
  data: () => ({
    name: null,
    password: null,
    showPassword: false,
    loading: false,
  }),
  computed: {
    enableButton () {
      return this.password && this.login
    }
  },
  methods: {
    login () {
      this.loading = true
      this.$axios.post('/administrator/login', {
        login: this.name,
        password: this.password
      }).then(response => {
        if (response.status === 200) {
          if (Number(response.data.codigo) === 200) {
            localStorage.moderatorToken = response.data.corpo
            setTimeout(() => {
              this.goTo('moderators-dashboard')
            }, 1000)
          } else this.setError(response.data.corpo)
        } else this.setError()
      }).catch(() => this.setError())
      .finally(() => this.loading = false);
    },
    setError (msg = 'Não foi possível logar') {
      this.showSnackbarErro(msg)
    }
  }
}
</script>

<style scoped>
.login { display: flex; justify-content: center; align-items: center; background: #000 !important; }
h1 { text-align: center; margin: 0; font-size: 1.8rem; }
.form { width: 340px; height: 300px; }
.autenticacao {
  min-height: 100vh;
  background-position: center;
  background-image: url(/fundo_autenticacao.png);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>